body {
    /* display: block; */
    /* width: 100%; */
    /* height: 100vh; */
    font-family: "Raleway", sans-serif;
    position: relative;
    /* background: #333; */
    overflow-x: hidden;
    font-family: "Roboto", sans-serif;
  }
  
  .slider_title {
    font-size: 3vw;
    color: gray;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 2vw;
  }
  
  .card-wrapper {
    position: relative;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
  }
  
  .card-wrapper:active,
  .card-wrapper:hover,
  .card-wrapper:focus {
    outline: none;
    border: none;
  }
  
  .card {
    width: 100%;
    height: 450px;
    border-radius: 16px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
  }
  
  .card .card-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: white;
    transition: 0.5s;
  }
  
  .card .card-image img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
  }
  
  .card:hover img {
    opacity: 0.4;
    transition: 0.5s;
  }
  
  .card:hover .card-image {
    transform: translateY(-100px);
    transition: all 0.9s;
  }
  
  .social-icons {
    position: absolute;
    top: 30%;
    left: 45%;
    transform: translate(-50%, -50%);
    z-index: 3;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .social-icons li {
    list-style: none;
  }
  
  .social-icons li a {
    position: relative;
    display: block;
    width: 200px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    /* background: gray; */
    font-size: 23px;
    color: #333;
    font-weight: bold;
    margin: 0 6px;
    transition: 0.9s;
    transform: translateY(200px);
    opacity: 0;
  }
  
  .card:hover .social-icons li a {
    transform: translateY(0px);
    opacity: 1;
    text-decoration: none;
  }
  
  .social-icons li a:hover {
    /* background: rgb(89, 77, 253); */
    transition: 0.2s;
  }
  
  .social-icons li a:hover .fa {
    color: #fff;
  }
  
  .social-icons li a .fa {
    transition: 0.8s;
  }
  
  .social-icons li a .fa:hover {
    /* 문구 마우스 오버시 색, 동작 */
    transform: rotateY(360deg);
    color: rgb(48, 48, 48);
  }
  
  .card:hover li:nth-child(1) a {
    transition-delay: 0.1s;
  }
  
  .card:hover li:nth-child(2) a {
    transition-delay: 0.2s;
  }
  
  .card:hover li:nth-child(3) a {
    transition-delay: 0.3s;
  }
  
  .card:hover li:nth-child(4) a {
    transition-delay: 0.4s;
  }
  
  .details {
    position: absolute;
    bottom: 400px;
    left: 0;
    background: #fff;
    width: 100%;
    height: 120px;
    z-index: 1;
    padding: 10px;
    transition: 0.6s;
  }
  
  .details h2 {
    margin: 30px 0;
    padding: 0;
    text-align: center;
  }
  
  .details h2 .job-title {
    font-size: 16px;
    line-height: 2;
    color: #333;
    font-weight: 300;
    display: block;
  }
  
  .card:hover .details {
    bottom: 0;
  }
  
